import React from "react";
import "./cardStyle.css"


//-L: Caso seja necessario colocar mais informaes adicione mais uma categoria
//no homePage na area dos cards e aqui tbm.
function Card (props){
    return (
        <div className="card">
            <p className="nome">{props.nome}</p>
            {/* <p>Descrio: {props.descricao}</p> */}
        </div>

    );
}

export default Card;