import React, { useState, useEffect } from 'react';
import { Chart as ChartJS, BarElement, CategoryScale, LinearScale, Tooltip } from 'chart.js';
import { Bar } from 'react-chartjs-2';
import './styleBar.css';
import axios from "axios";
import Logo from "../img/dashboard.png";
import { Input } from 'reactstrap';
import Card from "../card";

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Tooltip
)

var contador = 0

const Chart = () => {

    //Pega input
    const [value, setValue] = useState("");
    function handle() {
        console.log(value)
        return (url = `https://economia.awesomeapi.com.br/json/daily/${value}-BRL/15`)
    }

    console.log(handle())

    //var contador = 0

    //Muda a Url
    if (contador <= 1) {
        var url = `https://economia.awesomeapi.com.br/json/daily/USD-BRL/15`
        contador += 1
        console.log('contador:', contador)
    }
    else if (contador > 1) {

        url = handle()
        contador += 1
        console.log('contador:', contador)
    }




    const [product, setproduct] = useState(null)
    let content = null

    //Api
    useEffect(() => {
        axios.get(url)
            .then((response) => {
                setproduct(response.data)
                console.log("Deu certo")
            })

            .catch(() => {
                console.log("deu errado")
            })
    }, [url])



    var now = new Date

    if (product) {
        content =
            <div classname="tudo">
                <header>
                    <div className="tudo-um">
                        <nav>
                            {<img className="img-container" src={Logo} alt='' />}
                            <a>MURPHY DASHBOARD</a>

                            <ul class="nav-list"></ul>
                            
                            <div className='moeda'>
                                <Input type="text" className="caixa" class="form-control" placeholder="Digite o cdigo da moeda" aria-label="Cdigo da moeda" aria-describedby="basic-addon2" value={value} onChange={(e) => { setValue(e.target.value) }}></Input>
                            </div>
                        </nav>
                        <ul>
                            <li className="sub-titulo">Variao da Moeda</li>
                        </ul>
                    </div>
                    <div className="home">
                        <Card nome="USD" secnome='dolas americano' />
                        <Card nome="EUR" />
                        <Card nome="GBP" />
                        <Card nome="JPY" />
                        <Card nome="ARS" />
                        <Card nome="CAD" />
                        <Card nome="CNY" />
                        <Card nome="USD" />
                    </div>
                </header>

                <h1 className='abc'>{product[0].name}</h1>
                <div classname="grafico">
                    < Bar
                        data={{

                            labels: [now.getDate() - 14, now.getDate() - 13, now.getDate() - 12,
                            now.getDate() - 11, now.getDate() - 10, now.getDate() - 9,
                            now.getDate() - 8, now.getDate() - 7, now.getDate() - 6,
                            now.getDate() - 5, now.getDate() - 4, now.getDate() - 3,
                            now.getDate() - 2, now.getDate() - 1, now.getDate()],//legenda do grafico
                            datasets: [{
                                label: 'Cotao da Moeda',
                                data: [

                                    product[14].bid,
                                    product[13].bid,
                                    product[12].bid,
                                    product[11].bid,
                                    product[10].bid,
                                    product[9].bid,
                                    product[8].bid,
                                    product[7].bid,
                                    product[6].bid,
                                    product[5].bid,
                                    product[4].bid,
                                    product[3].bid,
                                    product[2].bid,
                                    product[1].bid,
                                    product[0].bid,
                                    6], // acrescenta mais um nmero para dizer o mximo do grafico
                                backgroundColor: [ //cores
                                    'rgba(13, 124, 255, 0.2)'

                                ],
                                borderColor: [ //borda mais escura
                                    'rgba(13, 124, 255, 1)'

                                ],
                                borderWidth: 1,
                                barPercentage: 0.7
                            }]
                        }}
                        width={400}
                        height={600} // Ajusta o tamanho do grafico



                        // plugins = {
                        //     {

                        //             fillStyle : 'blue'

                        //         }
                        // }

                        options={{
                            tooltip: true,
                            maintainAspectRatio: false,
                            scales: {
                                y: {
                                    beginAtZero: true
                                   
                                    
                                }
                            },

                            legend: {
                                labels: {

                                    fontSize: 30
                                    
                                    
                                }
                            }

                        }}
                    />
                </div>
            </div>
    }


    //Parte renderizada
    return (
        <div>{content}</div>
    );
}


export default Chart;